import LineHeightButton, { sideBarTriggerFactory, LineHeightButtonProps } from './components/lineHeightButton';

export default function toolbar(lineHeightConfig, sideBarTriggerConfig) {
  const lineheightLocaleConfig = lineHeightConfig.locale;
  return {
    buttons: {
      default: sideBarTriggerFactory<LineHeightButtonProps>(LineHeightButton, sideBarTriggerConfig),
    },
    locale: lineheightLocaleConfig,
  };
}
